import React, { useState, useEffect } from "react";
import axios from "axios";
import "./agregar.css";

const Agregarcodigo = ({ id }) => {
    const [codigo, setCodigo] = useState(""); // Estado para almacenar el cdigo a agregar
    const [mensaje, setMensaje] = useState(""); // Estado para mostrar mensajes de xito o error
    const [cargando, setCargando] = useState(false); // Estado para controlar la carga
    const [codigosUnicos, setCodigosUnicos] = useState(new Set()); // Estado para almacenar los cdigos de activacin nicos

    const urlMascotas = `https://smartpet-1d59e-default-rtdb.firebaseio.com/smartpet/mascotas.json`;
    const urlUsuario = `https://smartpet-1d59e-default-rtdb.firebaseio.com/usuario/${id}.json`;

    // Funcin para obtener los cdigos de activacin desde la base de datos de mascotas
    const fetchCodigosMascotas = async () => {
        try {
            const response = await axios.get(urlMascotas);
            const fetchedData = response.data;

            // Extrae y guarda los cdigos de activacin nicos en un set
            const codigos = new Set();
            Object.keys(fetchedData).forEach((key) => {
                if (fetchedData[key]?.codAct) {
                    codigos.add(fetchedData[key].codAct);
                }
            });
            return codigos; // Retorna el set de cdigos nicos de mascotas
        } catch (error) {
            console.error("Error al obtener datos de mascotas:", error);
            throw new Error("Error al obtener datos de mascotas");
        }
    };

    // Funcin para obtener los cdigos de activacin desde la base de datos de usuario
    const fetchCodigosUsuario = async () => {
        try {
            const response = await axios.get(urlUsuario);
            const fetchedData = response.data;

            // Extrae y guarda los cdigos de activacin nicos en un set
            const codigos = new Set();
            Object.keys(fetchedData).forEach((key) => {
                if (fetchedData[key]?.codAct) {
                    codigos.add(fetchedData[key].codAct);
                }
            });
            return codigos; // Retorna el set de cdigos nicos de usuario
        } catch (error) {
            console.error("Error al obtener datos de usuario:", error);
            throw new Error("Error al obtener datos de usuario");
        }
    };

    // Efecto para cargar los cdigos nicos al montar el componente y cuando 'id' cambia
    useEffect(() => {
        const fetchData = async () => {
            try {
                const codigosMascotas = await fetchCodigosMascotas();
                const codigosUsuario = await fetchCodigosUsuario();

                // Filtra los cdigos que estn en mascotas pero no en usuario
                const codigosFiltrados = Array.from(codigosMascotas).filter((codigo) => !codigosUsuario.has(codigo));
                setCodigosUnicos(new Set(codigosFiltrados)); // Actualiza el estado con los cdigos filtrados
            } catch (error) {
                console.error("Error al obtener cdigos nicos:", error);
            }
        };

        fetchData(); // Llama a la funcin fetchData al montar el componente y cuando 'id' cambia
    }, [id]);

    // Funcin para manejar el envo del formulario
    const handleSubmit = async (e) => {
        e.preventDefault(); // Evita el comportamiento predeterminado del formulario

        // Validacin del formato del cdigo
        const codigoValido = /^[a-zA-Z]{4}[0-9]{4}$/.test(codigo);
        if (!codigoValido) {
            setMensaje("El cdigo debe tener 4 letras seguidas de 4 nmeros.");
            return;
        }

        // Validacin de existencia del cdigo en los cdigos nicos
        if (codigosUnicos.has(codigo)) {
            setCargando(true); // Establece el estado de carga a verdadero

            try {
                // Realiza una solicitud POST para agregar el cdigo en la ruta especfica segn el 'id'
                const instance = axios.create({
                    baseURL: "https://smartpet-1d59e-default-rtdb.firebaseio.com", // URL base de la base de datos
                    timeout: 5000, // Tiempo mximo de espera para la solicitud en milisegundos
                    headers: {
                        "Content-Type": "application/json", // Tipo de contenido de la solicitud
                    },
                });

                const response = await instance.post(urlUsuario, {
                    codAct: codigo, // Datos a enviar en el cuerpo de la solicitud
                });

                console.log("Respuesta del servidor:", response.data);
                setMensaje("Cdigo agregado correctamente."); // Actualiza el estado del mensaje
            } catch (error) {
                console.error("Error al agregar cdigo:", error);
                setMensaje("Error al agregar cdigo. Por favor, intenta nuevamente."); // Actualiza el estado del mensaje de error
            }

            setCargando(false); // Establece el estado de carga a falso
        } else {
            setMensaje("El cdigo ingresado no es vlido o ya ha sido utilizado."); // Mensaje de error si el cdigo no es vlido o ya existe
        }
    };

    // Funcin para manejar cambios en el input de cdigo
    const handleChange = (e) => {
        setCodigo(e.target.value); // Actualiza el estado del cdigo con el valor del input
    };

    return (
        <section className="agregar-container section-validador">
            <div className="agregar-form-container">
                {cargando ? (
                    <div className="agregar-loading-container">
                        <p>Aguarde mientras se inicia la base de datos...</p>
                        <div className="agregar-loading-ring"></div>
                    </div>
                ) : (
                    <>
                        <h2 className="agregar-h2">Agregar Cdigo</h2>
                        <form onSubmit={handleSubmit} className="agregar-codigo-form">
                            <input
                                type="text"
                                id="codigo"
                                value={codigo}
                                onChange={handleChange}
                                placeholder="Ej. ABCD1234"
                                required
                            />
                            <button type="submit">Agregar</button>
                        </form>
                        {mensaje && (
                            <p className={`agregar-mensaje ${mensaje.includes("correctamente") ? "mensaje-exito" : ""}`}>
                                {mensaje}
                            </p>
                        )}
                    </>
                )}
            </div>
        </section>
    );
};

export default Agregarcodigo;
